<template>
  <div>
    <!-- 面包屑导航栏 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 警告区域 -->
      <el-alert title="添加商品信息" type="info" show-icon center></el-alert>

      <!-- 步骤条区域 -->
      <el-steps :space="200" :active="activeIndex-0" finish-status="success" align-center>
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>

      <!-- tab栏区域 -->
      <!-- v-model动态绑定的是name,让name的值与activeIndex一致，即可实现与步骤条的联动 -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef">

        <el-tabs :tab-position="'left'" v-model="activeIndex" :before-leave="beforeLeave" @tab-click="tabClicked">
          <!-- 基本信息 -->
          <el-tab-pane label="基本信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <br>
              <el-cascader
                v-model="addForm.goods_cat"
                :options="cateList"
                :props="cateProps"
                @change="handleChange"></el-cascader>
            </el-form-item>
          </el-tab-pane>

          <!-- 商品参数 -->
          <el-tab-pane label="商品参数" name="1">
            <!-- 渲染表单的item项，即获取到的动态参数 -->
            <el-form-item v-for="item in manyDataList" :key="item.attr_id" :label="item.attr_name">
              <br>
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="checkbox" v-for="(checkbox,i) in item.attr_vals " :key="i" border>
                </el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>

          <!-- 商品属性 -->
          <el-tab-pane label="商品属性" name="2">
            <el-form-item v-for="item in onlyDataList" :key="item.attr_id" :label="item.attr_name">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>

          <!-- 商品图片 -->
          <el-tab-pane label="商品图片" name="3">
            <!-- action 表示图片要上传到的后台API地址 -->
            <el-upload
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
             :headers="headersObj"
             :on-success="handleSuccess">
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>

          <!-- 商品内容 -->
          <el-tab-pane label="商品内容" name="4">
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <el-button type="primary" class="addBtn" @click="addGoods">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>

      </el-form>

    </el-card>

    <!-- 预览图片对话框 -->
    <el-dialog
      title="预览图片"
      :visible.sync="previewDialogVisible"
      width="50%">
      <img :src="previewPath" alt="" class="previewImg">
    </el-dialog>

  </div>
</template>

<script>
// 导入lodash
import _ from 'lodash'

export default {
  data () {
    return {
      // 步骤条正在进行项的id
      activeIndex: '0',
      // 表单数据对象
      addForm: {
        // 商品名称
        goods_name: '',
        // 价格
        goods_price: 0,
        // 重量
        goods_weight: 0,
        // 数量
        goods_number: 0,
        // 商品所属的分类数组,也就是选中的id数组,eg.选中1->3->2,就是[1,3,2],体现的是选中路径
        goods_cat: [],
        // 图片数组
        pics: [],
        // 商品介绍
        goods_introduce: '',
        // 一个关联当前分类的id和分类里面的属性值的数组
        attrs: []
      },
      // 表单验证规则
      addFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '请选择商品分类', trigger: 'blur' }
        ]
      },
      // 所有分类数据列表
      cateList: [],
      // 级联选择器的配置规则
      cateProps: {
        // 看到的-->选项名称
        label: 'cat_name',
        // 选中的-->该分类的id
        value: 'cat_id',
        // 父子关系
        children: 'children',
        // hover触发
        expandTrigger: 'hover'
      },
      // 动态参数列表数据
      manyDataList: [],
      // 静态属性列表数据
      onlyDataList: [],
      // 图片要上传到的API地址
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 拿到token
      headersObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 预览图片的完整地址
      previewPath: '',
      // 控制预览图片对话框的显示与隐藏
      previewDialogVisible: false
    }
  },
  created () {
    this.getCateList()
  },
  // 计算属性
  computed: {
    // 分类id
    cateId () {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  },
  methods: {
    // 获取所有分类数据列表
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error('获取分类数据失败')
      this.cateList = res.data
      // console.log(this.cateList)
    },
    // 级联选择器变化时，触发
    handleChange () {
      // console.log(this.addForm.goods_cat)
      // 我认为应该允许选择二级/三级分类，所以此处不做强制选择三级分类的要求
      if (this.addForm.goods_cat.length !== 3) {
        // 小于二级，清空级联选择器
        this.addForm.goods_cat = []
      }
    },
    // 页面切换触发函数
    beforeLeave (newName, oldName) {
      // newName :将要去往的页面
      // oldName :将要离开的页面
      if (this.addForm.goods_cat.length !== 3) {
        this.$message.error('请选择三级分类')
        // 阻止页面切换
        return false
      }
    },
    // 点击页面触发函数
    async tabClicked () {
      // 点击商品参数页面
      if (this.activeIndex === '1') {
        // 获取动态参数列表
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: 'many' } })
        // console.log(this.cateId)
        if (res.meta.status !== 200) return this.$message.error('获取参数列表失败')
        // 将attr_vals拆分成数组形式
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })
        this.manyDataList = res.data
      }
      // 点击商品属性页面
      else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: 'only' } })
        if (res.meta.status !== 200) return this.$message.error('获取属性列表失败')
        // console.log(res.data)
        this.onlyDataList = res.data
      }
      // 其他页面
      else {
        // console.log('other Page')
      }
    },
    // 图片预览效果
    handlePreview (file) {
      // console.log(file)
      this.previewPath = file.response.data.url
      this.previewDialogVisible = true
    },
    // 移除图片事件
    handleRemove (file) {
      // 移除的图片信息对象
      // console.log(file)

      // 1.获取要移除的图片路径tmp_path
      const filePath = file.response.data.tmp_path
      // 2.从pics数组中，找到该图片的索引值
      // x就是pics数组对象，pics数组里面有{pic:picPath}
      const i = this.addForm.pics.findIndex(x => x.pic === filePath)
      // console.log(i)
      // 3.删除这一项
      this.addForm.pics.splice(i, 1)
    },
    // 监听图片上传成功事件
    handleSuccess (response) {
      // console.log(response)
      // 1.将图片信息拼接成一个对象
      const picInfo = { pic: response.data.tmp_path }
      // 2.将图片信息对象push到pics数组中，每添加一张图片就push一项进pics[]
      this.addForm.pics.push(picInfo)
    },
    // 添加商品
    addGoods () {
      // 要先进行表单预验证
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return this.$message.error('请填写完整信息')

        // 进行数据处理
        // 对addForm进行深拷贝,将goods_cat数组-->字符串
        const newForm = _.cloneDeep(this.addForm)
        newForm.goods_cat = newForm.goods_cat.join(',')

        // 处理attrs数组
        // 对于动态参数
        this.manyDataList.forEach(item => {
          // 构造一个信息对象,存放当前分类id和属性值
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals.join(' ') }
          // 将这个对象push到attrs数组中
          this.addForm.attrs.push(newInfo)
        })
        // 对于静态属性
        this.onlyDataList.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })
        // 把整合的结果赋给newForm
        newForm.attrs = this.addForm.attrs
        console.log(newForm.attrs)

        // 进行添加商品操作
        const { data: res } = await this.$http.post('goods', newForm)
        console.log(res)
        if (res.meta.status !== 201) return this.$message.error('添加商品失败')
        this.$message.success('添加商品成功')
        this.$router.push('/goods')
      })
    }
  }
}
</script>

<style lang='less' scoped>
.previewImg{
  width: 100%;
}
// 添加商品按钮
.addBtn{
  margin-top: 15px;
}
</style>
